/* Fonts */

/* Main Styles */

html,
body {
    min-width: 360px;
    padding: 0;
    margin: 0;
    font-size: 100%;
    line-height: 1.5;
    color: #222222;
    font-family: 'Open sans', Arial, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Lato', 'Open sans', Arial, sans-serif;
}

.logo {
    display: inline-block;
    width: 40px;
    height: 40px;
    background-color: transparent;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    padding: 0;
    margin: 0;
    vertical-align: middle;
}

.logo img {
    max-width: 70%;
    height: auto;
    margin: auto;
    position: absolute;
    -webkit-transform: translate(-45%, -45%);
    transform: translate(-45%, -45%);
    left: 50%;
    top: 50%;
}

.logo+.text {
    display: inline-block;
    vertical-align: middle;
}

.approot {
    display: grid;
    grid-gap: 0;
    grid-template-columns: minmax(360px, 1fr) 4fr;
    padding: 0;
    margin: 0;
    min-height: 100vh;
}

.approot section {
    padding: 40px;
    margin: 0;
}

.approot .content {
    grid-column-start: 2;
    grid-row-start: 1;
    background-color: #f1f1f1;
}

.approot .content .content-inner {
    max-width: 45rem;
}

.approot .sidebar {
    grid-column-start: 1;
    grid-row-start: 1;
    background-color: gold;
}


/* Cards */

.card {
    position: relative;
    margin: 0rem 0 1.5rem 0;
    background-color: #fff;
    transition: box-shadow .25s;
    border-radius: 0 2px 2px 2px;
    box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.1), -1px -1px 1px 0px rgba(0, 0, 0, 0.05);
    padding: 20px;
}

.card .card-content {
    display: block;
    display: flow-root;
}

.card .card-content h4 {
    margin-top: 10px;
}

.company-wrapper {
    margin-bottom: 3rem;
}

.company-wrapper h3.company {
    margin: 0;
    background-color: #1E88E5;
    display: inline-block;
    padding: 0 8px;
    color: #ffffff;
}

.company-wrapper span.period {
    margin: 0 1rem 0 0;
    font-weight: 300;
}

.company-wrapper span.period::after {
    content: " | ";
    margin: 0 0 0 1rem;
}

.company-wrapper span.years {
    margin-left: 1rem;
}

.company-wrapper span.years::before {
    content: "(";
}

.company-wrapper span.years::after {
    content: ")";
}

/* Contact */

.contact-list {
    margin-bottom:2rem;
  }
  
  .contact-list > span {
      display: block;
      margin: 0 0 0.5rem 0;
      padding-bottom:0.5rem;
  
  }
  
  .contact-list > span .label{
      display:block;
      font-size:0.8rem;
      opacity:0.85;
  }
  
  .contact-list > span .value {
    display:inline-block;
  }
  
  .contact-list > span a.value {
    -webkit-text-decoration:underline dotted;
    text-decoration:underline dotted;
    color:#000000;
    border-radius:2px; 
  }


/* skills list */

ul.inlined-list {
    list-style: none;
    margin: 1rem 0;
    padding: 0;
}

ul.inlined-list li {
    display: inline-block;
    margin: 3px;
    padding: 2px 6px;
    background-color: rgba(255, 255, 255, 0.75);
    border-radius: 2px;
    font-size: 0.85rem;
}


/* Languages */

.languages-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.languages-list li {
    padding: 5px 0 10px 0;
    margin: 0 0 5px 0;
    border-bottom: 1px solid rgba(0, 0, 0, .08);
}

.lang-prof {
    font-size: 0.8rem;
    display: block;
    opacity: 0.75;
}

@Media all and (max-width: 1024px) {
    .approot {
        grid-template-columns: minmax(280px, 1fr) 4fr;
    }
}

@Media all and (max-width: 768px) {
    .approot {
        grid-template-columns: 1fr;
    }
    .approot .content {
        grid-column-start: 1;
        grid-row-start: 1;
    }
    .approot .sidebar {
        grid-column-start: 1;
        grid-row-start: 2;
        background-color: gold;
    }
}
